* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

header {
  z-index: 999;
  width: 100vw;
  height: 80px;
  background-color: rgba(0, 0, 0, .4);
  justify-content: center;
  align-items: center;
  padding: 0 25px;
  display: flex;
  position: fixed;
  top: 0;
}

.header-ct-nav-logo {
  width: 1400px;
  z-index: 10;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  display: flex;
}

.logo-menubar {
  font-family: var(--font-hand);
