  align-items: center;
  display: flex;
}

.main-navigation {
  width: 400px;
  white-space: nowrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  display: flex;
}

.main-navigation li a {
  font-size: 25px;
  font-family: var(--font-hand);
  color: var(--color-menu);
  padding: 5px 0;
}

.main-navigation li a:hover {
  color: var(--hover-menu-btn);
}

.current {
  border-bottom: 2px solid var(--hover-menu-btn);
}

.menu-button, .js-menu-container {
  display: none;
}

@media screen and (max-width: 1157px) {
  header {
    padding: 0 15px;
  }

  .header-ct-nav-logo {
    max-width: 100%;
    padding: 0;
  }

  .logo-menubar {
    font-size: 35px;
  }

  .logo {
    width: 320px;
  }

  .main-navigation {
    width: 350px;
  }

  .js-menu-container, .menu-button, .js-menu-container {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .menu-button {
    width: 50px;
    height: 50px;
    display: flex;
  }

  .main-navigation {
    display: none;
  }

  .logo-menubar {
    font-size: 25px;
  }

  .logo {
    max-width: 250px;
  }

  .logo-menubar {
    padding-right: 3%;
  }

  .logo img {
    width: 70px;
  }

  .menu-button svg use {
    fill: var(--hover-menu-btn);
  }

  .js-menu-container {
    display: none;
  }

  .js-menu-container.is-open {
    width: 100%;
    height: 100%;
    z-index: 10;
    background-color: #fff;
    background-image: url("wall-hexa1.20b8bade.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 40px 5%;
    display: flex;
    position: fixed;
    top: 0;
    right: 0;
  }

  .js-menu-container .js-close-menu {
    cursor: pointer;
    background: none;
    border: none;
    align-self: flex-end;
  }

  .js-menu-container .js-close-menu svg {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 10px;
    right: 15px;
  }

  .js-menu-container .js-close-menu svg use {
    fill: var(--hover-menu-btn);
  }

  .content-menu-modal {
    flex-direction: column;
    justify-content: space-between;
    display: flex;
  }

  .main-navigation-modal a {
    font-size: 40px;
    font-family: var(--font-hand);
    color: var(--color-menu);
  }
}

